* {
    box-sizing: border-box;
}

.noSelect:focus {
    outline: none !important;
}
h1, p {
    margin: 0;
    padding-top: 30px;
}

::selection {
	background-color: transparent;
}

.noSelect {
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.noSelect:focus {
    outline: none !important;
}

body {
    margin:  0;
    padding: 0;
    background-color: black;
    color: rgba(0,0,0,0.4);
    height: 100vh;
}

#root, .App {
    height: 100%;
}

main {
    background-image: url(../imgs/background.jpg);
    height: 100%;
}

@keyframes game-over {
    0% {
        background-color: rgba(244, 86, 86, 0.285);
        width: 260px;
        height: 260px;
        transform: translatey(0px);
    }
    
    25% {
        background-color: rgba(255, 255, 0, 0.242);
        width: 290px;
        height: 290px;
        transform: translatey(-10px);
    }
    
    50% {
        background-color: rgba(0, 0, 255, 0.152);
        width: 260px;
        height: 260px;
        transform: translatey(0px);
    }
    
    75% {
        background-color: rgba(0, 255, 42, 0.152);
        width: 290px;
        height: 290px;
        transform: translatey(-10px);
    }
    
    100% {
        background-color: rgba(255, 0, 179, 0.152);
        width: 260px;
        height: 260px;
        transform: translatey(0px);
    }
}

@keyframes start-over {
    0% {
        background-color: rgba(244, 86, 86, 0.285);
    }
    
    25% {
        background-color: rgba(255, 255, 0, 0.242);
    }
    
    50% {
        background-color: rgba(0, 0, 255, 0.152);
    }

    75% {
        background-color: rgba(0, 255, 42, 0.152);
    }

    100% {
        background-color: rgba(255, 0, 179, 0.152);
    }
}

.start-over {
    border: unset;
    width: 260px;
    height: 70px;
    border-radius: 2vw;
    animation-name: start-over;
    animation-timing-function: linear;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    color: rgba(255,255,255,0.5);
    cursor: pointer;
    font-size: 24px;
    font-weight: 600;
}

.game-btn-container {
    height: 310px;
}


.game-btn {
    box-sizing: border-box;
    width: 270px;
    height: 270px;
    border-radius: 50%;
    border: 1px solid rgb(128, 128, 128);
    cursor: pointer;
    margin-top: 30px;
    font-size: 40px;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    position:relative;
}

.game-over {
    pointer-events: none;
    animation-name: game-over;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

.hide{
    visibility: hidden;
}

.btn-txt {
    position:absolute;
    left:50%;
    padding:0;
    margin:0;
    top:50%;
    transform:translate(-50%,-50%);
    font-size:40px;
    white-space:nowrap;
}